:root {
  --primary-color: #00ccc5;
  --secondary-color: #181818;
  --glass-bg: rgba(255, 255, 255, 0.048); /* Transparent white for glass */
  --glass-border: rgba(255, 255, 255, 0.2); /* Soft white border */
}

* {
  margin: 0;
  padding: 0;
  background: var(--secondary-color);
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 120px 0px 120px ;
  background-color: transparent;
  position: sticky;
  top: 0;
  z-index: 999; /* Make sure it's above other content */
}
.nav-links{
  display: flex;
  gap: 20px;
}
.nav-link{
  font-size: 18px;
  text-decoration: none;
  cursor: pointer;
  position: relative;
  color: var(--primary-color);
  padding-bottom: 4px;
  display: inline-block;
}
.nav-link::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0%;
  height: 2px;
  background-color: var(--primary-color);
  transition: width 0.4s ease-in-out;
}
.nav-link:hover::after {
  width: 100%;
}
.logo {
  font-size: 25px;
  font-weight: 800;
  font-style: italic;
  color: var(--primary-color);
}
.contact-content{
  color: var(--primary-color);
}
.home-content {
  color: var(--primary-color);
}
.home-container {
 margin: 0px 100px 0px 100px;
}
.intro-text{
  position: absolute;
  top: 30%;
  left: 24%;
  padding: 0;
  margin: 0;
}

.name-text{
  font-size: 90px;
  text-align: center;
  padding: 0;
  margin: 0;
}

.profile-content {
  display: flex;
  font-style: italic;
  background-color: var(--glass-bg);
  height: 100vh;
  z-index: 1;
  flex-direction: column;
  justify-content: center;

  color: var(--primary-color);
}
.skills-container{
  display: flex;
  justify-content: center;
  gap: 20px;

}
